<template>
	<div id="detailContent">
		<load v-if="istrue"></load>
		<div v-else>
			<hello title="电影详情">
				<span class="headerBack" @touchstart = "handleBack"><</span>
			</hello>

	        <div class="lose">
	    		<div class="bigImg">
		   			 <div class="detailBimg" :style="{backgroundImage:'url('+detailmovie.images.medium+')'}"></div>
		    	     <div class="bigDetail">
		    	     	<div>
		    	     		<img :src="detailmovie.images.small" style="margin: 20px;width:110px;height: 150px;" />
		    	     	</div>
		    	     	<div class="osti">
		    	     		<h2 style="font-size: 19px;color: #dadcdf;">{{detailmovie.title}}</h2>
		    	     		<p>主演：<b v-for='item in detailmovie.casts'>  {{item.name}}</b></p>
		    	     		<p>类型：{{detailmovie.genres | jion}}</p>
		    	     		<p>时长：{{detailmovie.durations | jion}}</p>
		    	     		<p>上映时间：{{detailmovie.pubdate}}</p>
		    	     	</div>
		    	     </div>
	    		</div>
	    		<div class="loseDown">
	    			<div class="loseDowmTOP">
	    				<p style="color: green;font-size: 18px;padding: 5px 0px;">{{detailmovie.title}}简介:
	    				</p>
	    				<p style="text-indent: 2em;">{{detailmovie.summary}}
	    				</p>
	    				<p style="color: green;font-size: 18px;padding: 5px 0px;">相关图片：
	    				</p>
	    				<ul style="padding: 6px;">
	    					<li v-for="item in detailmovie.photos">
	    						<img :src="item.icon" style="width: 100%;" />
	    					</li>
	    				</ul>
	    			</div>
	    		</div>
	    	</div>
    	</div>
	</div>
</template>
<script>
	import hello from "@/components/HelloWorld"
	export default{
		name:"detail",
		data(){
			return{
				istrue:true,
				detailmovie:{}

			}
		},
		components:{hello},
		props:['setid'],
		methods:{
			handleBack(){
				/*返回前一项*/
				this.$router.back();
			}
		},
		mounted(){
			const setId = this.setid;
			this.axios.get('https://douban.uieee.com/v2/movie/subject/'+setId).then((rel) =>{
				const msg = rel.statusText;
				if(msg == 'OK'){
					this.detailmovie = rel.data;
					this.istrue = false;
				}
			});
		}
	}
</script>
<style scoped>
	*{margin: 0;padding: 0;}
	ul li{list-style-type: none;}
	#detailContent{
		position: absolute;
		left: 0;
		top: 0;
		z-index: 10;
		min-height: 100%;
		width: 100%;
		background: white;
	}
	.lose{
		width: 100%;
		margin-top: 50px;	
	}
	.bigImg{
		width: 100%;
		height: 240px;
	}
	.bigDetail{
		position: absolute;
		width: 100%;
		display: flex;
	}
	.osti{
		flex: 1;
		margin-top: 20px;
	}
	.osti>p{
		margin-top: 3px;
		font-size: 16px;
		color: #dadcdf;
	}
	.detailBimg{
		position: absolute;
		width: 100%;
		height: 200px;
		z-index: -1;
		/*背景图模糊*/
		filter: blur(20px);
		/*设置背景图片的大小，下面让背景图全显示出来*/
		background-position:100% 240px; 
		background-size: cover;
	}
	.loseDown{
		height: calc( 100vh - 310px );
		padding: 10px;
		overflow: scroll;
	}
	.headerBack{
		position: absolute;
		padding-left: 5px;
		padding-right: 5px;
		font-size: 30px;
		font-weight: bold;
		line-height: 50px;
	}
	
</style>


